<!--
    
   snackbar position===0
   1===0positiontopDirection ===1downDirection
   V 0.0.1
  -->

<!--html-->
<template>
  <div class="snackbar">
    <transition :name="directionData[direction]"
                v-on:after-leave="afterLeave"
    >
      <div class="snackbar-wrapper" v-show="isShow" :class="{bottom:position ===1}">
        <div class="content border-1px">{{content}}</div>
        <div class="btn" @click="hide">{{btnText}}</div>
      </div>
    </transition>
  </div>
</template>
<!--script-->
<script type="text/ecmascript-6">
  export default {
    data(){
      return {
        directionData: ["top-direction", "down-direction", "left-direction", "right-direction"],// 
        isShow: false, // 
        content: "", // 
        timer: undefined, // 
        callBack: undefined, // 
        isClick: false
      }
    },
    props: {
      position: {
        type: Number,
        default: 0 //   0  1
      },
      direction: {
        type: Number,
        default: 0 //  0   1 2  3 
      },
      btnText: {
        type: String,
        default: ""
      },
      displayDuration: {//5
        type: Number,
        default: 5000
      }
    },
    methods: {
      //content:callBack:
      show(content, callBack){
        if (this.isShow)//
          return;
        // 
        this.isClick = false;
        this.callBack = callBack;
        this.content = content;
        //
        if (this.timer) {
          clearTimeout(this.timer)
        }
        let that = this;
        // 
        that.isShow = true;
        // 5 
        this.timer = setTimeout(function () {
          that.isShow = false;
        }, this.displayDuration);
      },
      // 
      hide(){
        this.isShow = false;
        //
        this.isClick = true;
      },
      //
      afterLeave(){
        if (this.callBack && this.isClick)
          this.callBack();
      }
    }
  }
</script>
<!--css-->
<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  ::-webkit-scrollbar
    width 0
    height 0

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5)
    .border-1px
      &::after
        -webkit-transform scaleY(0.7)
        transform scaleY(0.7)

  @media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2)
    .border-1px
      &::after
        -webkit-transform scaleY(0.5)
        transform scaleY(0.5)

  .snackbar
    .snackbar-wrapper
      position fixed
      top 0
      left 0
      display flex
      width 100%
      height 50px
      background #373A41
      transition all 0.5s
      z-index 2147483647
      &.bottom
        top auto
        bottom 0
      &.top-direction-enter-active, &.top-direction-leave-active,
      &.left-direction-enter-active, &.left-direction-leave-active,
      &.right-direction-enter-active, &.right-direction-leave-active,
      &.down-direction-enter-active, &.down-direction-leave-active
        opacity 1
        transform translate3d(0, 0, 0)
      &.top-direction-enter, &.top-direction-leave-active
        opacity 0
        transform translate3d(0, -100%, 0)
      &.left-direction-enter, &.left-direction-leave-active
        opacity 0
        transform translate3d(-100%, 0, 0)
      &.right-direction-enter, &.right-direction-leave-active
        opacity 0
        transform translate3d(100%, 0, 0)
      &.down-direction-enter, &.down-direction-leave-active
        opacity 0
        transform translate3d(0, 100%, 0)
      &.bottom
        &.right-direction-enter, &.right-direction-leave-active
          opacity 0
          transform translate3d(100%, 0, 0)
        &.down-direction-enter, &.down-direction-leave-active
          opacity 0
          transform translate3d(0, 100%, 0)
      .content, .btn
        color #fff
        height 50px
        line-height 50px
        font-size 14px
        user-select: none
        -webkit-user-select none
      .content
        flex 1
        height 100%
        text-align left
        padding 0 10px
        text-overflow ellipsis
        display block
        overflow hidden
        white-space nowrap
        position relative
        &::after
          position absolute
          right 0
          bottom 0
          display block
          height 100%
          border-right 1px solid rgba(255, 255, 255, .2)
          content ''
      .btn
        flex 0 0 80px
        text-align center
        user-select none
        -webkit-user-select none
        touch-callout: none
        -webkit-touch-callout none
        &:active
          color rgba(255, 255, 255, .6)
</style>



